import React, { useCallback } from 'react'
import {
  BigScreen,
  getGlobalStyle,
  SmallScreen
} from '../../../../helpers'
import {
  Divider,
  Icon,
  Link,
  Text
} from '../../../atoms'
import {
  Column,
  Container,
  Row
} from '../../../layout'
import { HeaderLinks } from '../HeaderPrototype'
import { PopUpButton } from '../PopUpButton'
import styles from './SalesOffers.module.css'

export interface SalesOffersProps {
  activeColor?: string;
  activeMenu?: string;
  isActive?: boolean;
  linkWrapper?: React.FunctionComponent;
  offersLinks?: HeaderLinks[];
  site?: 'unimarc' | 'alvi'
  onClickMenu?: () => void;
  handleOpenMenu?: (buttonPressed: string) => void;
}

export const SalesOffers = ({
  activeColor,
  isActive,
  linkWrapper,
  offersLinks = [],
  site,
  onClickMenu,
  handleOpenMenu
}: SalesOffersProps) => {

  const handleClick = useCallback(() => {
    !isActive && handleOpenMenu('salesOffer')
    isActive && handleOpenMenu('')
    onClickMenu && onClickMenu()
  },[isActive])

  return(
    <>
      <BigScreen>
        <PopUpButton
          buttonActiveColor={activeColor}
          buttonFirstLine='Ofertas'
          buttonFirstLineSize='lg'
          buttonFirstLineWeight='semibold'
          buttonSite={site}
          isActive={isActive}
          onClickButton={handleClick}
          popUpPos='middleRight'
        >
          {(Array.isArray(offersLinks) && offersLinks) &&
            <Column padding='5px'>
              {offersLinks.map( link => {
                return (
                  <Link
                    clickable={true}
                    href={link.url}
                    key={`${link.title}__${link.url}`}
                    underline='hover'
                    wrapper={linkWrapper}
                  >
                    <Text
                      clickable='pointer'
                      fontSize='lg'
                      fontWeight='regular'
                      lineHeight='30px'
                    >
                      {link.title}
                    </Text>
                  </Link>
                )
              })}
            </Column>
          }
        </PopUpButton>
      </BigScreen>
      <SmallScreen>
        <>
          <Text
            color='white'
            fontSize='md'
            onClick={handleClick}
          >
            Ofertas
          </Text>
          {isActive &&
            <Container position='fixed' zIndex='99'>
              <Column
                backgroundColor='white'
                customHeight='100vh'
                position='fixed'
              >
                <Row
                  alignItems='center'
                  boxShadow='0 0 8px 0 rgba(27, 33, 56, 0.06)'
                  justifyContent='between'
                  minHeight='70px'
                  padding='0 30px'
                >
                  <Text fontSize='2xl' fontWeight='semibold'>
                    Ofertas
                  </Text>
                  <Icon
                    color='black'
                    customSize={20}
                    name='Close'
                    onClick={handleClick}
                  />
                </Row>
                <Column padding='20px'>
                  {(Array.isArray(offersLinks) && offersLinks) && offersLinks.map( link => {
                    return (
                      <React.Fragment key={`${link.title}__${link.url}`}>
                        <Link
                          clickable={true}
                          customClassName={styles['mobile-link']}
                          href={link.url}
                          underline='none'
                          wrapper={linkWrapper}
                        >
                          <Row
                            alignItems='center'
                            background='red'
                            customWidth='100'
                            justifyContent='between'
                          >
                            <Text
                              clickable='pointer'
                              color='black'
                              fontSize='md'
                            >
                              {link.title}
                            </Text>
                            <Icon
                              clickable='pointer'
                              color={getGlobalStyle('--color-icons-gray')}
                              name='ArrowRightNavigate'
                              sizes='xs'
                            />
                          </Row>
                        </Link>
                        <Divider />
                      </React.Fragment>
                    )
                  })}
                </Column>

              </Column>
            </Container>
          }
        </>
      </SmallScreen>
    </>
  )
}
