import { getGlobalStyle } from '../../../../../helpers'
import { Container } from '../../../../layout'
import styles from './PopUp.module.css'

export interface PopUpDialogProp {
  arrowPos?: 'left' | 'center' | 'right';
  children?: React.ReactNode;
  position?: 'left' | 'middleLeft' | 'center' | 'middleRight' | 'right';
}

export const PopUpDialog = ({
  arrowPos,
  children,
  position
}: PopUpDialogProp) => {
  const setPopUpPosition = () => {
    if (position) return `${styles['popup']} ${styles[position]}`
    return styles['popup']
  }

  const setPopUpArrowPosition = () => {
    if (arrowPos) return `${styles['popupCorner']} ${styles[arrowPos]}`
    return styles['popupCorner']
  }
  
  return (
    <>
      <span className={setPopUpArrowPosition()}></span>
      <Container
        backgroundColor='white'
        borderRadius='15px'
        className={setPopUpPosition()}
        customWidth='max-content'
        justifyContent='center'
        minWidth='220px'
        padding='10px'
        position='absolute'
        zIndex={getGlobalStyle('--z-index-10')}
      >
        {children}
      </Container>
    </>
  )
}