import { useCallback } from 'react'
import { BigScreen, SmallScreen } from '../../../../helpers'
import { Column } from '../../../layout'
import { MyProfileContent } from '../../Header/MyProfile/MyProfileContent'
import { PopUpButton } from '../PopUpButton'
import { ToggleButton } from '../PopUpButton/ToggleButton'

export interface MyProfileHeaderProps {
  activeColor?: string;
  isActive?: boolean;
  isLoggedIn?: boolean;
  isVisibleModalLogout?: boolean;
  isVisibleMyProfile?: boolean;
  site?: 'unimarc' | 'alvi'
  userName?: string;
  handleOpenMenu?: (buttonPressed: string) => void;
  handlePushTag?: () => void;
  onClickCloseMyProfile?: () => void;
  onClickCloseLogoutMobile?: () => void;
  onClickMenu?: () => void;
  onClickGoToMyCoupons?: () => void;
  onClickGoToMyLists?: () => void;
  onClickGoToMyOrders?: () => void;
  onClickLogin: () => void;
  onClickLogout?: () => void;
  onClickLogoutMobile?: () => void;
}

export const MyProfileHeader = ({
  activeColor,
  isActive,
  isLoggedIn,
  isVisibleModalLogout,
  site,
  userName,
  handleOpenMenu,
  handlePushTag,
  onClickCloseLogoutMobile,
  onClickGoToMyCoupons,
  onClickGoToMyLists,
  onClickGoToMyOrders,
  onClickLogin,
  onClickLogout,
  onClickLogoutMobile
}: MyProfileHeaderProps) => {
  const profileArrow = isLoggedIn ? true : false

  const handleClick = useCallback(() => {
    if (!isLoggedIn){
      handlePushTag && handlePushTag()
      onClickLogin()
    }else{
      handleOpenMenu(isActive ? '' : 'myProfile')
    }
  },[isActive, isLoggedIn])

  const handleCloseMoblie = useCallback(() => {
    handleOpenMenu('')
  },[])

  return(
    <>
      <BigScreen>
        <PopUpButton
          buttonActiveColor={activeColor}
          buttonFirstLine='¡Hola!'
          buttonSecondLine={userName ? userName : 'Inicia sesión'}
          buttonSecondLineWeight='semibold'
          buttonShowArrow={profileArrow}
          buttonSite={site}
          buttonWidth='112px'
          isActive={isActive}
          isLoggedIn={isLoggedIn}
          onClickButton={handleClick}
          popUpArrowPos='right'
          popUpPos='middleRight'
        >
          <Column padding='5px'>
            <MyProfileContent
              onClickGoToMyCoupons={onClickGoToMyCoupons}
              onClickGoToMyLists={onClickGoToMyLists}
              onClickGoToMyOrders={onClickGoToMyOrders}
              onClickLogout={onClickLogout}
              site={site}
            />
          </Column>
        </PopUpButton>
      </BigScreen>
      <SmallScreen>
        <>
          <ToggleButton
            activeColor={activeColor}
            firstLine='¡Hola!'
            firstLineSize='sm'
            isActive={isActive}
            isLoggedIn={isLoggedIn}
            onClick={handleClick}
            secondLine={userName ? userName : 'Inicia sesión'}
            secondLineSize='sm'
            secondLineWeight='semibold'
            showArrow={profileArrow}
            site={site}
          />
          <MyProfileContent
            isVisibleModalLogout={isVisibleModalLogout}
            isVisibleMyProfile={isActive}
            onClickCloseLogoutMobile={onClickCloseLogoutMobile}
            onClickCloseMyProfile={handleCloseMoblie}
            onClickGoToMyCoupons={onClickGoToMyCoupons}
            onClickGoToMyLists={onClickGoToMyLists}
            onClickGoToMyOrders={onClickGoToMyOrders}
            onClickLogout={onClickLogout}
            onClickLogoutMobile={onClickLogoutMobile}
            site={site}
          />
        </>
      </SmallScreen>
    </>
  )
}