import React, {
  useCallback,
  useEffect,
  useState
} from 'react'
import {
  BigScreen,
  getGlobalStyle,
  SmallScreen
} from '../../../../helpers'
import {
  Backbone,
  Icon,
  Link,
  Picture,
  Text
} from '../../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../layout'
import { CategoryLink } from './CategoryLink'
import style from './Categories.module.css'

export interface CategoryData {
  children?: CategoryData[];
  image?: string;
  img?: string;
  fq?: string;
  hasChildren: boolean;
  id: number;
  MetaTagDescription?: string;
  name: string;
  url: string;
}
export interface CategoriesData {
  data: CategoryData[],
  getCategoryUrl?: (category?: CategoryData) => string;
  onHoverCategory?: (category?: CategoryData) => void;
}

interface CategoriesProps {
  activeColor?: string;
  categoriesData?: CategoriesData;
  linkWrapper?: React.FunctionComponent;
  isActive?: boolean;
  isActiveOpen?: boolean;
  site?: 'unimarc' | 'alvi';
  handleOpenMenu?: (buttonPressed: string) => void;
}

export const Categories = ({
  activeColor,
  categoriesData,
  isActive,
  linkWrapper,
  site = 'unimarc',
  handleOpenMenu
}: CategoriesProps) => {
  const [categoriesClasses, setCategoriesClasses] = useState([style['categories-nav'], style['categories-hidden']])
  const [isSubMenuOpen, setIsSubMenuOpen] = useState(false)
  const [subMenuClasses, setSubMenuClasses] = useState([style['nav-sub-menu'], style['nav-sub-menu-hiden']])
  const [subcategory, setSubcategory] = useState(null)
  const defaultBackground = site === 'unimarc' ? getGlobalStyle('--color-background-primary') : getGlobalStyle('--color-alvi-primary-blue')
  const customBackground = activeColor || defaultBackground
  const [buttonBackground, setButtonBackground] = useState('')
  const subMenuContainer = subcategory?.CategoryData ? style['submenu-container'] : `${style['submenu-container']} ${style['no-banner']}`

  const changeActive = useCallback(() => {
    !isActive && handleOpenMenu('categories')
    isActive && handleOpenMenu('')
    setIsSubMenuOpen(false)
    setSubcategory(null)
  },[isActive])

  const handleCloseMenu = useCallback(() => {
    handleOpenMenu('')
  },[isActive])

  const handleEnter = useCallback(() => {
    !isActive && setButtonBackground(customBackground)
  },[isActive])

  const handleOut = useCallback(() => {
    !isActive && setButtonBackground('')
  },[isActive])

  useEffect(() => {
    const activeMainClasses = [style['categories-nav']]
    const activeSubClasses = [style['nav-sub-menu'], style['nav-sub-menu-hiden']]
    !isActive && activeMainClasses.push(style['categories-hidden'])
    setCategoriesClasses(activeMainClasses)
    setSubMenuClasses(activeSubClasses)
  },[isActive])

  useEffect(() => {
    const activeClasses = [style['nav-sub-menu']]
    !isSubMenuOpen && activeClasses.push(style['nav-sub-menu-hiden'])

    setSubMenuClasses(activeClasses)
  }, [isSubMenuOpen])

  useEffect(() => {
    document.body.style['overflow-y'] = isActive ? 'hidden' : 'overlay'
  }, [isActive])

  useEffect(() => {
    isActive ? setButtonBackground(customBackground) : setButtonBackground('')
  },[isActive])

  return(
    <>
      <BigScreen>
        <>
          {/* Menu button */}
          <Container
            alignItems='center'
            backgroundColor={buttonBackground}
            borderRadius='100px'
            clickable='pointer'
            customWidth='max-content'
            justifyContent='center'
            minHeight='50px'
            onClick={changeActive}
            onMouseEnter={handleEnter}
            onMouseLeave={handleOut}
            padding='0 20px'
          >
            <Container
              clickable='pointer'
              customClassName={style['icon-container']}
              customHeight='16px'
              margin='0 0 10px 0'
              width='21px'
            >
              <Text
                clickable='pointer'
                customClassName={isActive && style['open']}
                type='span'
              >
              </Text>
              <Text
                clickable='pointer'
                customClassName={isActive && style['open']}
                type='span'
              >
              </Text>
              <Text
                clickable='pointer'
                customClassName={isActive && style['open']}
                type='span'
              >
              </Text>
            </Container>
            <Spacer.Vertical size={8} />
            <Text
              clickable='pointer'
              color='white'
              customClassName={style['categories-text']}
              fontWeight='semibold'
            >
            Categorías
            </Text>
          </Container>
          {/* Open menu */}
          {categoriesData?.data &&
            <Container
              backgroundColor={getGlobalStyle('--color-neutral-gray-white')}
              className={categoriesClasses.join(' ')}
              padding='40px 0'
              position='absolute'
              width='305px'
            >
              {categoriesData?.data?.map((category) => {
                return(
                  <React.Fragment key={category?.id}>
                    <CategoryLink
                      categoriesData={categoriesData}
                      category={category}
                      handleClickCategory={handleCloseMenu}
                      linkWrapper={linkWrapper}
                      setIsSubMenuOpen={setIsSubMenuOpen}
                      setSubcategory={setSubcategory}
                      site={site}
                      subcategory={subcategory}
                    />
                  </React.Fragment>
                )
              })}
            </Container>
          }
          {/* subMenu */}
          <Container
            backgroundColor='white'
            customClassName={subMenuClasses.join(' ')}
            position='absolute'
            width='768px'
          >
            {subcategory?.desktopImage &&
              <Container customHeight='150px' customWidth='768px'>
                <Picture
                  alt={subcategory.name}
                  border='none'
                  borderRadius='none'
                  height='100%'
                  src={subcategory.desktopImage}
                  width='100%'
                />
              </Container>
            }
            {subcategory &&
              <Column className={subMenuContainer} padding='40px 44px'>
                <Row alignItems='center' margin='0 0 28px 0'>
                  <Link
                    href={categoriesData.getCategoryUrl(subcategory)}
                    onClick={handleCloseMenu}
                    underline='none'
                    wrapper={linkWrapper}
                  >
                    <Text
                      clickable='pointer'
                      color='primary'
                      fontSize='3xl'
                    >
                      {subcategory?.name}
                    </Text>
                  </Link>
                  <Spacer.Vertical size={16} />
                  <Spacer.Vertical size={4} />
                  <Link
                    href={categoriesData.getCategoryUrl(subcategory)}
                    onClick={handleCloseMenu}
                    underline='none'
                    wrapper={linkWrapper}
                  >
                    <Text
                      clickable='pointer'
                      fontSize='md'
                      fontWeight='semibold'
                      underline='always'
                    >
                      Ver todo
                    </Text>
                  </Link>
                </Row>
                <Row isWrap>
                  {subcategory?.children?.map((list) => {
                    return (
                      <React.Fragment key={list?.id}>
                        <Column
                          customWidth='33'
                          margin='0 0 20px 0'
                          padding='0 10px 0 0'
                        >
                          <Link
                            href={categoriesData.getCategoryUrl(list)}
                            onClick={handleCloseMenu}
                            underline='none'
                            wrapper={linkWrapper}
                          >
                            <Text
                              clickable='pointer'
                              customClassName={style['sublist-title']}
                              fontSize='lg'
                              fontWeight='semibold'
                            >
                              {list.name}
                            </Text>
                          </Link>
                          {list?.children?.map((sublist) => {
                            return(
                              <React.Fragment key={sublist?.id}>
                                <Link
                                  href={categoriesData.getCategoryUrl(sublist)}
                                  onClick={handleCloseMenu}
                                  underline='hover'
                                  wrapper={linkWrapper}
                                >
                                  <Text
                                    clickable='pointer'
                                    color='gray'
                                    customClassName={style['sublist-text']}
                                    fontSize='md'
                                  >
                                    {sublist.name}
                                  </Text>
                                </Link>
                              </React.Fragment>
                            )
                          })}
                          <Link
                            href={categoriesData.getCategoryUrl(list)}
                            onClick={handleCloseMenu}
                            underline='none'
                            wrapper={linkWrapper}
                          >
                            <Text
                              clickable='pointer'
                              fontSize='md'
                              fontWeight='semibold'
                              underline='always'
                            >
                              Ver todo
                            </Text>
                            <Spacer.Horizontal size={16}/>
                          </Link>
                        </Column>
                      </React.Fragment>
                    )
                  })}
                </Row>
              </Column>
            }
          </Container>
        </>
      </BigScreen>
      <SmallScreen>
        <>
          <Icon
            color='white'
            name='Hamburger'
            onClick={changeActive}
            sizes='sm'
          />
          {isActive &&
            <Container
              backgroundColor='white'
              className={style['categories-main-mobile']}
              customHeight='100hv'
              position='absolute'
              top='0'
              zIndex={getGlobalStyle('--z-index-50')}
            >
              <Row
                alignItems='center'
                customClassName={style['mobile-title']}
                justifyContent='between'
                minHeight='70px'
                padding='0 30px'
              >
                <Text fontSize='2xl' fontWeight='semibold'>
                  Nuestras Categorías
                </Text>
                <Icon
                  color='black'
                  customSize={20}
                  name='Close'
                  onClick={handleCloseMenu}
                />
              </Row>
              {categoriesData?.data &&
                <Row
                  customClassName={style['mobile-items']}
                  isWrap
                  padding='10px 5px'
                >
                  {categoriesData?.data?.map((category) => {
                    return (
                      <React.Fragment key={category?.id}>
                        <Container
                          alignItems='center'
                          background='green'
                          justifyContent='center'
                          margin='0 0 20px 0'
                          maxWidth='33%'
                        >
                          <Link
                            href={categoriesData.getCategoryUrl(category)}
                            onClick={handleCloseMenu}
                            underline='none'
                            wrapper={linkWrapper}
                          >
                            <Column
                              alignItems='center'
                              background='green'
                              justifyContent='center'
                              margin='0 0 10px 0'
                            >
                              {category?.img
                                ? <Container
                                  backgroundColor={getGlobalStyle('--color-neutral-anti-flash')}
                                  borderRadius='50px'
                                  justifyContent='center'
                                  maxHeight='100px'
                                  maxWidth='100px'
                                >

                                  <Picture
                                    alt={category?.name}
                                    height='100px'
                                    src={category?.img}
                                    width='100px'
                                  />
                                </Container>
                                : <Backbone
                                  borderRadius='50%'
                                  height={100}
                                  width={100}
                                />
                              }
                              <Text fontSize='md' textAlign='center'>{category.name}</Text>
                            </Column>
                          </Link>
                        </Container>
                      </React.Fragment>
                    )
                  })}
                </Row>
              }
            </Container>
          }
        </>
      </SmallScreen>
    </>
  )
}