import { Icons } from '../../../../assets/SvgComponents/SvgIcons'
import { getGlobalStyle } from '../../../../helpers'
import { Icon, Link } from '../../../atoms'
import { Column } from '../../../layout'
import styles from './LoginButton.module.css'

export interface LogoButtonProps{
  linkWrapper?: React.FunctionComponent;
  logo?: keyof (typeof Icons);
  logoUrl?: string;
  isMobile?: boolean;
  onClick?: () => void;
}

export const LogoButton = ({
  linkWrapper,
  logo = 'LogoHeader',
  logoUrl,
  isMobile = false,
  onClick
}: LogoButtonProps) => {
  const logoSize = isMobile ? 107 : 150

  return (
    <>
      <Column customWidth='max-content' margin='0 auto 0 0'>
        <Link
          customClassName={styles['logo-header']}
          href={logoUrl}
          label='Home'
          onClick={onClick}
          wrapper={linkWrapper}
        >
          <Icon
            clickable='pointer'
            color={getGlobalStyle('--color-icons-white')}
            customSize={logoSize}
            name={logo}
          />
        </Link>
      </Column>
    </>
  )
}