import { Column, Container } from '../../../../layout'
import {
  FontSize,
  Icon,
  Text
} from '../../../../atoms'
import { getGlobalStyle } from '../../../../../helpers'
import {
  useCallback,
  useEffect,
  useState
} from 'react'
import styles from './ToggleButton.module.css'

interface ToggleButtonProps {
  activeColor?: string;
  firstLine: string;
  firstLineSize?: FontSize;
  firstLineWeight?: 'regular' | 'semibold' | 'medium' | 'bold';
  isActive: boolean;
  isLoggedIn?: boolean;
  showArrow?: boolean
  secondLine?: string;
  secondLineSize?: FontSize;
  secondLineWeight?: 'regular' | 'semibold' | 'medium' | 'bold';
  site?: 'unimarc' | 'alvi';
  text?: string[];
  width?: string;
  onClick?: () => void;
}

export const ToggleButton = ({
  activeColor,
  firstLine,
  firstLineSize = 'md',
  firstLineWeight = 'regular',
  isActive = false,
  isLoggedIn,
  showArrow = false,
  secondLine,
  secondLineSize = 'md',
  secondLineWeight = 'regular',
  site,
  width,
  onClick
}: ToggleButtonProps) => {
  const defaultBackground = site === 'unimarc' ? getGlobalStyle('--color-background-primary') : getGlobalStyle('--color-alvi-primary-blue')
  const customBackground = activeColor || defaultBackground
  const [buttonBackground, setButtonBackground] = useState('')

  const handleClick = useCallback(() => {
    onClick()
  },[isLoggedIn, isActive])

  const handleEnter = useCallback(() => {
    !isActive && setButtonBackground(customBackground)
  },[isActive])

  const handleOut = useCallback(() => {
    !isActive && setButtonBackground('')
  },[isActive])

  useEffect(() => {
    isActive ? setButtonBackground(customBackground) : setButtonBackground('')
  },[isActive])

  return (
    <Container
      alignItems='center'
      backgroundColor={buttonBackground}
      borderRadius='100px'
      clickable='pointer'
      customWidth='max-content'
      margin='0 5px'
      minHeight='50px'
      minWidth={width}
      onClick={handleClick}
      onMouseEnter={handleEnter}
      onMouseLeave={handleOut}
      padding={!showArrow ? '0 15px' : '0 10px 0 15px'}
    >
      <Column clickable='pointer' justifyContent='start'>
        <Text
          clickable='pointer'
          color='white'
          customClassName={styles['toggle-button-text']}
          fontSize={firstLineSize}
          fontWeight={firstLineWeight}
          lineHeight='15px'
        >
          {firstLine}
        </Text>
        <Text
          clickable='pointer'
          color='white'
          customClassName={styles['toggle-button-text']}
          fontSize={secondLineSize}
          fontWeight={secondLineWeight}
        >
          {secondLine}
        </Text>
      </Column>
      {showArrow &&
        <Column clickable='pointer' margin='-3px 0 0 5px'>
          <Icon
            clickable='pointer'
            color='white'
            customSize={14}
            name={!isActive ? 'DropDownClose' : 'DropDownOpen'}
          />
        </Column>
      }
    </Container>
  )
}