import { useCallback } from 'react'
import {
  BigScreen,
  getGlobalStyle,
  SmallScreen
} from '../../../../helpers'
import { Icon, Text } from '../../../atoms'
import {
  Column,
  Row,
  Spacer
} from '../../../layout'
import { PopUpButton } from '../PopUpButton'

export interface MyShoppingProps {
  activeColor?: string;
  isActive?: boolean;
  isLoggedIn?: boolean;
  site?: 'unimarc' | 'alvi'
  handleOpenMenu?: (buttonPressed: string) => void;
  onClickLogin?: () => void;
  onClickMenu?: () => void;
  onClickMyShopping?: () => void;
  onClickGoToMyOrders?: () => void;
  onClickRepeatLastOrder?: () => void;
}

export const MyShopping = ({
  activeColor,
  isLoggedIn,
  site,
  onClickGoToMyOrders,
  onClickLogin,
  onClickMyShopping,
  onClickRepeatLastOrder
}: MyShoppingProps) => {

  const handleGoToMyShopping = useCallback(() => {
    if (!isLoggedIn) {
      onClickMyShopping && onClickMyShopping()
      onClickLogin()
    } else {
      onClickGoToMyOrders()
    }
  },[isLoggedIn])

  return(
    <>
      <BigScreen>
        <PopUpButton
          buttonActiveColor={activeColor}
          buttonFirstLine='Mis'
          buttonFirstLineWeight='semibold'
          buttonSecondLine='Compras'
          buttonSecondLineWeight='semibold'
          buttonShowArrow={false}
          buttonSite={site}
          isActive={false}
          isLoggedIn={isLoggedIn}
          onClickButton={handleGoToMyShopping}
          popUpArrowPos='right'
          popUpPos='middleRight'
        >
          <Column padding='5px'>
            <Row
              alignItems='center'
              clickable='pointer'
              onClick={handleGoToMyShopping}
              padding='4px 0'
            >
              <Icon
                clickable='pointer'
                color={getGlobalStyle('--color-icons-black')}
                customSize={20}
                name='BagTick'
              />
              <Spacer.Vertical size={12} />
              <Text
                clickable='pointer'
                color='black'
                fontSize='md'
                underline='hover'
              >
                Mis compras
              </Text>
            </Row>
            <Row
              alignItems='center'
              clickable='pointer'
              hidden={true}
              onClick={onClickRepeatLastOrder}
              padding='4px 0'
            >
              <Icon
                clickable='pointer'
                color={getGlobalStyle('--color-icons-black')}
                customSize={20}
                name='Repeat'
              />
              <Spacer.Vertical size={12} />
              <Text
                clickable='pointer'
                color='black'
                fontSize='md'
                underline='hover'
              >
                Repetir última compra
              </Text>
            </Row>
          </Column>
        </PopUpButton>
      </BigScreen>
      <SmallScreen>
        <Text
          color='white'
          fontSize='md'
          onClick={handleGoToMyShopping}
        >
          Mis compras
        </Text>
      </SmallScreen>
    </>
  )
}